.contact-container {
    display: grid;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    grid-template-columns: 600px;
    margin-top: 40px;
}

#info-contact {
    text-align: center;
}

#social-links-container {
    margin-top: 60px;
}

i {
    padding: 20px;
    font-size: 40px;
}

i:hover {
    color: #f25bba;
}

#email-send {
    font-size: 24px;
}

#email-send:hover {
    font-size: 22px;
}



